/* eslint-disable */
import Vue from 'vue'

/* init root component */
import {initRequestCenterDisplay} from "../core/registry_network";
import store from "../core/registry_vuex";
import {mapGetters} from "vuex";

const kpiapp = new Vue({
    el: "#kpiapp-library-container",
    mounted: function(){
        window.app = this
        setTimeout(function(){
            initRequestCenterDisplay()
        },100)
        this.fetchOrganization()
        this.fetchUserActor()
    },
    methods: {
        fetchOrganization: function() {
            var self = this;
            self.$cloudjetRequest.ajax({
                url: '/api/organization',
                method: 'get',
                success: function(data){
                    // set organization object
                    self.$store.dispatch('organizationActions',data)
                }
            })
        },
        fetchUserActor: function() {
            var self = this;
            self.$cloudjetRequest.ajax({
                url: '/api/profile',
                method: 'get',
                success: function(data){
                    // set user object
                    self.$store.dispatch('userActorActions',data)
                }
            })
        },
        fetchUserViewed: function() {
            var self = this;
        },
        fetchUserActorSubordinates: function() {
            var self = this;
        }
    },
    store,
    computed: {
        ...mapGetters([
            'userActor',
            'userViewed',
            'organization',
            'userActorSubordinates',
            // ...
        ])
    }
})
window.kpiAppLibrary = kpiapp