<template>
    <div class="component-wrapper">
        <div id="company-performance-score"
             class="col-sm-4 col-table-4 kpi-panel text-center">
            <div class="kpi-panel-body">
                <div class="kpi-panel-header">{{ "Company Information" | translate }}</div>
                <div class="container-fluid">
                    <div class="rows-table-sm text-center">
                        <div class="col-sm-6 col-table-6">
                            <div class="rows-table-sm">
                                <div class="logo padding-top-15">
                                    <img :alt="organization_object.name"
                                         :src="organization_object.get_logo"
                                         :title="organization_object.name" />
                                </div>
                            </div>
                            <div class="bt-mission-box">
                                <button class="btn btn-normal btn-fullwidth btn-cus-5-7 HOME_BTN_VISION" id="btn-vision-misson" data-toggle="modal" data-target="#modal-vision-mission">
                                    {{ "Vision & Mission" | translate }}</button>
                            </div>
                        </div>
                        <div class="col-sm-6 col-table-6" id="performance-quater">
                            <div>
                                    <span class="com-per-title">{{ "Performance" | translate }} - {{ "Quarter" | translate }}
                                        <span>{{ current_quarter.quarter }}</span>
                                    </span><br>
                                <span class="com-per-pecent">
                                            <span id="company-performance-quarter">{{ company_performance.kpi_percent|scoreDisplay }}</span><span class="per-com-unit"></span>
                                    </span>
                            </div>
                        </div>
                    </div>
                </div>
                <hr class="per-border border-top" />
                <div class="container-fluid text-left due-box">
                    <div class="col-sm-6" id="">
                        <div>{{ "Due" | translate }}</div>
                        <div>{{ "Allow review" | translate }}</div>
                    </div>
                    <div class="col-sm-6 due-date-color">
                        <div>{{ current_quarter.due_date | dateTimeFormat }}</div>
                        <div v-if="userActor.is_admin">
                            <select v-model="organization_object.monthly_review_lock" class="HOME_DDL_ALLOW_MONTH"
                                    @change="update_current_organization()">
                                <option value="allow_all">{{ "All months" | translate }}</option>
                                <option value="1">{{ current_quarter.month_1_name }}</option>
                                <option value="2">{{ current_quarter.month_2_name }}</option>
                                <option value="3">{{ current_quarter.month_3_name }}</option>
                            </select>
                        </div>
                        <div v-else>
                            <span v-if="organization_object.monthly_review_lock === '1'"> {{ current_quarter.month_1_name }}</span>
                            <span v-if="organization_object.monthly_review_lock === '2'"> {{ current_quarter.month_2_name }}</span>
                            <span v-if="organization_object.monthly_review_lock === '3'"> {{ current_quarter.month_3_name }}</span>
                            <span v-if="organization_object.monthly_review_lock === 'allow_all'">{{ "All months" | translate }}</span>
                        </div>
                    </div>
                </div>
                <div class="table-per-com-result">
                    <table class="table table-bordered table-striped text-center">
                        <thead>
                        <tr>
                            <th>{{ current_quarter.month_1_name }}</th>
                            <th>{{ current_quarter.month_2_name }}</th>
                            <th>{{ current_quarter.month_3_name }}</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr>
                            <td>
                                <span class="">{{ company_performance.month_1_score|scoreDisplay }}</span>
                            </td>
                            <td>
                                <span class="">{{ company_performance.month_2_score|scoreDisplay }}</span>
                            </td>
                            <td>
                                <span class="">{{ company_performance.month_3_score|scoreDisplay }}</span>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    // https://medium.com/3yourmind/large-scale-vuex-application-structures-651e44863e2f
    // https://github.com/igeligel/vuex-feature-scoped-structure
    // https://github.com/igeligel/vuex-namespaced-module-structure
    // https://morningstar.engineering/managing-your-vue-js-applications-state-with-vuex-part-2-5e5c3a0cd507
    import { mapGetters } from "vuex";

    import { cloneObject } from 'sdk/utils/object'
    import { getUrlVars } from 'sdk/utils/url'
    export default {
        name: "company_performance",
        watch: {
            organization: function(oldVal,newVal){
                if (newVal !== undefined){
                    this.$root.$emit('preDataReady')
                }
            }
        },
        mounted: function(){
            var self = this;
            self.$root.$on('preDataReady',function(){
                self.organization_object = cloneObject(self.organization)
                self.get_company_performance()
                self.get_quarter_by_id()
            })
        },
        data: function(){
            return {
                organization_object: {},
                company_performance: {},
                current_quarter: {},

            }
        },
        beforeDestroy:function(){
            this.$root.$off('preDataReady')
        },
        computed: {
            ...mapGetters([
                'userActor',
                'organization',
            ])
        },
        methods: {
            get_quarter_by_id: function () {
                var self = this;
                var quarter_id = getUrlVars()['quarter_id'];
                var url = '/api/quarter/?get_current_quarter=yes';
                if (quarter_id !== undefined) {
                    url += '&quarter_id=' + quarter_id;
                }
                cloudjetRequest.ajax({
                    type: 'get',
                    url: url,
                    success: function (data) {
                        var clonedData = cloneObject(data['fields'])
                        clonedData.month_1_name = data['month_1_name'];
                        clonedData.month_2_name = data['month_2_name'];
                        clonedData.month_3_name = data['month_3_name'];
                        self.$set(self,'current_quarter', clonedData);
                    },
                    error: function () {
                        alert('error');
                    },
                    complete: function (data) {
                    }
                });
            },
            get_company_performance: function () {
                var self = this
                var quarter_id = getUrlVars()['quarter_id'];
                var url = '/api/user_kpi/' + self.organization.ceo + '/';
                if (quarter_id !== undefined) {
                    url += '?quarter_id=' + quarter_id;
                }
                cloudjetRequest.ajax({
                    type: 'get',
                    url: url,
                    success: function (res) {
                        self.$set(self,'company_performance', res)
                    },
                    error: function (res) {
                    }
                });
            },
            update_current_organization: function() {
                var self = this;
                console.log('update_current_organization');
                cloudjetRequest.ajax({
                    url: "/api/organization/",
                    type: 'post',
                    data: JSON.stringify(self.organization_object),
                    success: function (data) {
                        self.$store.dispatch('organizationActions',data)
                    },
                    error: function () {
                        alert('Update organization Error');
                    }
                });
            },
        }
    }
</script>

