/* eslint-disable */
import Vue from 'vue'
import { formatDateToString } from 'sdk/utils/datetime_format'

Vue.filter('dateTimeFormat', value => {
  return !value
    ? ''
    : formatDateToString(new Date(value.toString()))
})

Vue.filter('scoreDisplay', val => {
    // model -> view
    // formats the value when updating the input element.
    return typeof(val) === 'number' ? (val === 0 ? "0%" : (val.toFixed(2) + "%")) : "0%";

});