function toLocaleDateStringNumeric(datestring,timezone="en-US"){
  if (datestring !== null && datestring !== undefined) return (new Date(datestring).toLocaleDateString(timezone,  { year: 'numeric', month: 'numeric', day: 'numeric' }));
  return ""
}

function extractFileName(fileURL) {
    return fileURL.substring(fileURL.lastIndexOf('/')+1);
}
function formatDateToString(date){
    // 01, 02, 03, ... 29, 30, 31
    var dd = (date.getDate() < 10 ? '0' : '') + date.getDate();
    // 01, 02, 03, ... 10, 11, 12
    var MM = ((date.getMonth() + 1) < 10 ? '0' : '') + (date.getMonth() + 1);
    // 1970, 1971, ... 2015, 2016, ...
    var yyyy = date.getFullYear();

    // create the format you want
    return (dd + "/" + MM + "/" + yyyy);
}
function formatDateToTimeString(date){
    // 01, 02, 03, ... 29, 30, 31
    var hh = date.getHours()
    // 01, 02, 03, ... 10, 11, 12
    var mm = ((date.getMinutes() + 1) < 10 ? '0' : '') + (date.getMinutes());
    // 1970, 1971, ... 2015, 2016, ...
    var ss =((date.getSeconds() + 1) < 10 ? '0' : '') + (date.getSeconds());
    // create the format you want
    return (hh + ":" + mm + ":" + ss);
}

function getDateStandard(date){
    // 01, 02, 03, ... 29, 30, 31 aaaa
    // Hello
    var dd = (date.getDate() < 10 ? '0' : '') + date.getDate();
    // 01, 02, 03, ... 10, 11, 12completed_date
    var MM = ((date.getMonth() + 1) < 10 ? '0' : '') + (date.getMonth() + 1);
    // 1970, 1971, ... 2015, 2016, ...
    var yyyy = date.getFullYear();

    // create the format you want
    return (yyyy + "-" + MM + "-" + dd);
}
export {
    toLocaleDateStringNumeric,
    extractFileName,
    getDateStandard,
    formatDateToTimeString,
    formatDateToString,
}