<template>
  <div class="notification" ref="notificationWrapper">
    <notification_bell
      @click="toggleNotificationList"
      :isToggle="isShowNotificationList"
      :notificationCount="notificationData.unread_list.length"
    />
    
    <notification_list
      @lazyLoad="notificationListOnLazyLoad"
      v-if="isShowNotificationList"
      class="notification__list"
      :notificationItems="notificationData.results"
      :href="href"
      :next="notificationData.next"
    />   
  </div>
</template>

<script>
import notificationBell from "../notification_bell/notification_bell";
import notificationList from "../notification_list/notification_list";
import Vue from "vue";

export default {
  components: {
    notification_bell: notificationBell,
    notification_list: notificationList
  },

  methods: {
    /**
     * Input new lazy load data here
     * Automatically assign next + concat notification
     */
    // concatNotificationData (notificationData) {
    //   this.next = notificationData.next
    //   this.notificationItems = this.notificationItems.concat(notificationData.results)
    // },

    notificationListOnLazyLoad(next) {



      /**
       * Write unit test for this shit
       */
      if (this.onLazyLoad) {
        this.onLazyLoad(next)
      }
    },

    toggleNotificationList() {
      this.isShowNotificationList = !this.isShowNotificationList;

      /**
       * Emmit markAsRead
       * Params: unread_list
       */
      if (this.notificationData.unread_list.length > 0) {
        this.$emit("markAsRead", this.notificationData.unread_list);
        this.notificationCount = 0;
      }
    },

    hideNotificationList() {
      this.isShowNotificationList = false;
    },

    /**
     * Todo: Unit Test
     * isShowNotificationList = true and click in document should set to false
     * isShowNotificationList = false and click in document should persist to false
     */
    handleClickOutSide(e) {
      let el = this.$refs.notificationWrapper;
      let target = e.target;

      /**
       * If it's click outside
       */
      if (el !== target && !el.contains(target)) {
        if (this.isShowNotificationList) this.isShowNotificationList = false;
      }
    }
  },

  created() {
    Vue.nextTick(() => {
      /**
       * Hide notification on click outside
       */
      document.addEventListener("click", this.handleClickOutSide);

      /**
       * Vue doesn't allow to edit props
       */
      this.notificationCount = this.notificationData.unread_list.length;
    });
  },

  destroyed() {
    /**
     * Remove on added event
     */
    document.removeEventListener("click", this.handleClickOutSide);
  },

  data() {
    return {
      isShowNotificationList: false,
      notificationCount: 0
    };
  },

  props: {
    /**
     * Pass anything the api return in results
     * into this. will format data automatically
     */
    notificationData: {
      type: Object
    },

    /**
     * Link to show all notification
     */
    href: {
      type: String
    },

    onLazyLoad: Function
  }
};
</script>

<style scoped lang="scss">
.notification {
  position: relative;
  display: flex;
  justify-content: flex-end;

  &__list {
    position: absolute;
    top: 50px;
  }
}
</style>
